import {createCustomElement} from '@servicenow/ui-core';
import snabbdom, {createRef} from '@servicenow/ui-renderer-snabbdom';
import view from './view.js';

// Action handlers
import docViewerActionHandlers from './actionHandlers.js';

// Styles
import docViewerStyles from './document-viewer.scss';
import pdfViewerStyles from 'pdfjs-dist/web/pdf_viewer.css';

// Constants
import {FETCH_ATTACHMENT_METADATA, SYS_ID, VIEWER_HIDDEN, SEARCH_STATES, MESSAGE_NO_PREVIEW, MAX_FILE_SIZE, DOCVIEWER_CLOSE_ACTION_CLICKED} from './constants.js';

/**
 * Seismic component to render a document.
 *
 * Usage: <sn-document-viewer sys-id='{sysId}' rotate='false'/>
 *
 * Features: Search, Navigate, Zoom, Rotate, Download.
 *
 * Provides a view to download attachment when it cannot be rendered.
 *
 * @seismicElement sn-document-viewer
 * @summary Seismic component to render a document.
 */
createCustomElement('sn-document-viewer', {
	renderer: {
		type: snabbdom,
		// Loading view from view.js
		view
	},
	// Initializing state
	initialState: {
		fileName: '',
		containerRef: createRef(),
		fileSize: -1,
		sizeLoaded: 0,
		pdfViewer: '',
		totalPages: 0,
		currentPage: 0,
		passwordError: '',
		searchString: '',
		searchState: SEARCH_STATES.NOT_REQUESTED,
		searchInputRef: createRef(),
		noPreview: 'false',
		noPreviewMessage: MESSAGE_NO_PREVIEW,
		disableDownload: 'false',
		fileNameRef: null,
		searchButtonRef: null,
		prevSearchButtonRef: null,
		nextSearchButtonRef: null,
		clearSearchButtonRef: null,
		prevPageButtonRef: null,
		nextPageButtonRef: null,
		zoomOutButtonRef: null,
		zoomInButtonRef: null,
		rotateClockwiseButtonRef: null,
		rotateAnticlockwiseButtonRef: null,
		downloadButtonRef: null,
		closeButtonRef: null,
		password: '',
		useBrowserViewer: 'false',
		deleteContainerHidden: 'true',
		deleteErrorHidden: 'true',
		matchesCount: -1,
		currentMatch: -1,
		textData: '',
		isImage: 'false',
		convertedSysId: ''
	},
	// Receives sysId as property
	properties: {
		/**
		 * A string containing sys_id of the attachment to be previewed.
		 *
		 * @type {String}
		 */
		sysId: {default: ''},
		/**
		 * If set to true, Search functionality will not be available.
		 *
		 * @type {Boolean}
		 */
		searchDisabled: {default: 'false'},
		/**
		 * If set to true, Navigation support will not be available.
		 *
		 * @type {Boolean}
		 */
		navigationDisabled: {default: 'false'},
		/**
		 * If set to true, Zoom Out/Zoom In functionality will not be available.
		 *
		 * @type {Boolean}
		 */
		zoomDisabled: {default: 'false'},
		/**
		 * If set to true, Rotate clockwise/anticlockwise functionality will not be available.
		 *
		 * @type {Boolean}
		 */
		rotationDisabled: {default: 'false'},
		/**
		 * If set to true, Download functionality will not be available.
		 *
		 * @type {Boolean}
		 */
		downloadDisabled: {default: 'false'},
		/**
		 * If set to true, Close button will be hidden.
		 *
		 * @type {Boolean}
		 */
		closeHidden: {default: 'false'},

		/**
		 *  If set to true, delete button will be hidden
		 */
		deleteHidden: {default: 'false'},

		/**
		 * If set to true, toolbar will be hidden.
		 *
		 * @type {Boolean}
		 */
		toolbarHidden: {default: 'false'},
		/**
		 * If set to true, viewer will be hidden.
		 *
		 * @type {Boolean}
		 */
		viewerHidden: {default: 'false'},
		/**
		 * Maximum file size in MB that can be viewed, if not specified default value of 50MB is considered.
		 *
		 * @type {String}
		 */
		maximumFileSize: {default: MAX_FILE_SIZE},
		/**
		 * If set to true, viewer will render in dark mode
		 *
		 * @type {Boolean}
		 */
		darkMode: {default: 'false'},
		/**
		 * If set to true, office file types can be converted and viewed
		 *
		 * @type {Boolean}
		 */
		renderOfficeFormat: {default: 'false'}
	},
	// Fetch and Render PDF once element is initially connected and bootstrapped
	onBootstrap(host, dispatch) {
		if (host[VIEWER_HIDDEN] == 'false')
			dispatch(FETCH_ATTACHMENT_METADATA, {sysId: host[SYS_ID]});
	},
	dispatches: {
		/**
		 * This is dispatched onBootstrap
		 */
		FETCH_ATTACHMENT_METADATA: {
			stopPropagation: true
		},
		/**
		 * This is dispatched from the effect on success of fetchAttachmentMetadata
		 */
		SET_TOOLBAR_STATE: {
			stopPropagation: true
		},
		/**
		 * This is dispatched from setToolbarState
		 */
		VALIDATE_METADATA: {
			stopPropagation: true
		},
		/**
		 * This is dispatched from validateMetadata post validation
		 */
		FETCH_AND_LOAD_PDF: {
			stopPropagation: true
		},
		/**
		 * This is dispatched if the fetch failed
		 */
		FETCH_FAILED: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user clicks on Previous button
		 */
		PREVIOUS_PAGE: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user clicks on Next button
		 */
		NEXT_PAGE: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when page is changed
		 */
		PAGE_CHANGED: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user types a value in page number input
		 */
		PAGE_NUMBER_CHANGED: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user clicks on zoom out button
		 */
		ZOOM_OUT: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user clicks on zoom in button
		 */
		ZOOM_IN: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user clicks on rotate clockwise button
		 */
		ROTATE_CLOCKWISE: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user clicks on rotate anticlockwise button
		 */
		ROTATE_ANTICLOCKWISE: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user enters a password
		 */
		SUBMIT_PASSWORD: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user enters a string for search
		 */
		SEARCH_CONTENT: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user clicks for prev search result
		 */
		PREV_SEARCH_RESULT: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user clicks for next search result
		 */
		NEXT_SEARCH_RESULT: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when user wants to clear search
		 */
		CLEAR_SEARCH: {
			stopPropagation: true
		},
		/**
		 * This is dispatched when preview can not be shown
		 */
		SHOW_NO_PREVIEW: {
			stopPropagation: true
		},

		SHOW_DELETE_CONFIRMATION: {
			stopPropagation: true
		},

		CLOSE_DELETE_CONFIRMATION: {
			stopPropagation: true
		},

		DELETE_ATTACHMENT: {
			stopPropagation: true
		},

		DELETE_ATTACHMENT_UTIL: {
			stopPropagation: true
		},

		DELETE_ATTACHMENT_ERROR: {
			stopPropagation: true
		},

		CLOSE_DOCUMENT_VIEWER: {
			stopPropagation: true
		},

		[DOCVIEWER_CLOSE_ACTION_CLICKED] : {
		},

		GET_ATTACHMENTS_ACCESS_INFO: {
			stopPropagation: true
		},

		UPDATE_ATTACHMENT_DELETE_INFO: {
			stopPropagation: true
		},

		GET_TEXT_DATA: {
			stopPropagation: true
		},

		SET_TEXT_DATA: {
			stopPropagation: true
		},

		CONVERT_DOCUMENT_TO_PDF: {
			stopPropagation: true
		},

		UPDATE_VIEWER_DOCUMENT: {
			stopPropagation: true
		},

		PDF_CONVERSION_FAILED: {
			stopPropagation: true
		}
	},
	// Loading actionHandlers from actions.js
	...docViewerActionHandlers,
	// Loading styles from document-viewer.scss
	styles: [docViewerStyles, pdfViewerStyles]
});
