import {createHttpEffect} from '@servicenow/ui-effect-http';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import pdfjsLib from 'pdfjs-dist';
import worker from 'pdfjs-dist/build/pdf.worker';
import * as pdfjsViewer from 'pdfjs-dist/web/pdf_viewer';
import * as viewerConstants from './constants.js';

/**
 * Fetches Attachment Metadata
 */
const fetchAttachmentMetadata = createHttpEffect(viewerConstants.ATTACHMENT_METADATA_URL, {
	method: viewerConstants.GET_METHOD,
	pathParams: [viewerConstants.SYS_ID],
	successActionType: viewerConstants.SET_TOOLBAR_STATE,
	errorActionType: viewerConstants.FETCH_FAILED,
	batch: false
});

/**
 * Get all the attachments access info
 */
const getAttachmentsAccessInfo =  createGraphQLEffect(viewerConstants.ATTACHMENT_GRAPHQL_QUERY, {
	variableList: ['table', 'sysId'],
	successActionType: viewerConstants.UPDATE_ATTACHMENT_DELETE_INFO
});

/**
 * Filter and update the attachment delete info
 */
const updateAttachmentDeleteInfo = ({action, state, updateProperties}) => {
	if (action.payload && action.payload.data) {
		const docAttachments = action.payload.data.query.attachments._results
			.filter(attachment => attachment.sysId === state.properties['sysId']);
		if (docAttachments.length > 0)
			if (docAttachments[0].canDelete)
				updateProperties({deleteHidden: 'false'});
			else
				updateProperties({deleteHidden: 'true'});
	}
};

/**
 * Get the text data
 */
const getTextData = createHttpEffect(viewerConstants.ATTACHMENT_CONTENT_API_PREFIX + viewerConstants.SYS_ID_PARAM + viewerConstants.ATTACHMENT_CONTENT_API_SUFFIX, {
	method: viewerConstants.GET_METHOD,
	pathParams: [viewerConstants.SYS_ID],
	successActionType: viewerConstants.SET_TEXT_DATA,
	batch: false
});

/**
 * Set the text data
 */
const setTextData = ({action, updateState}) => {
	if (action.payload)
		updateState({textData: action.payload});
};

/**
 * Convert the given document to PDF format
 */
const convertDocumentToPDF = createHttpEffect(viewerConstants.CONVERT_TO_PDF_URL, {
	method: viewerConstants.POST_METHOD,
	dataParam: 'data',
	successActionType: viewerConstants.UPDATE_VIEWER_DOCUMENT,
	errorActionType: viewerConstants.PDF_CONVERSION_FAILED,
	batch: false
});

/** 
 * Update the property 'convertedSysId' and reload viewer with converted pdf document
 */
const updateViewerDocument = ({action, dispatch, updateState}) => {
	if (action.payload.result && action.payload.result.converted_attachment_id) {
		updateState({convertedSysId: action.payload.result.converted_attachment_id});
		dispatch(viewerConstants.FETCH_ATTACHMENT_METADATA, {sysId: action.payload.result.converted_attachment_id});
	} else
		// Failure scenario for PDF Conversion
		dispatch(viewerConstants.SHOW_NO_PREVIEW, {failure_message: viewerConstants.DOCUMENT_NO_PREVIEW, disable_download: 'false'});
};

/**
 * Handle failure scenarios for PDF Conversion
 */
const handlePDFConversionFailure = ({dispatch}) => {
	dispatch(viewerConstants.SHOW_NO_PREVIEW, {failure_message: viewerConstants.DOCUMENT_NO_PREVIEW, disable_download: 'false'});
};

/**
 * Returns the default properties for files rendered by browser
 */
const getDefaultBrowserViewerProperties = () => {
	return {
		searchDisabled: 'true',
		navigationDisabled: 'true',
		zoomDisabled: 'true',
		rotationDisabled: 'true'
	};
};

/**
 * Checks if the content type is supported
 */
const fileTypeSupported = (contentType) => {
	if (viewerConstants.PDFJS_VIEWER_SUPPORTED_MIME_TYPES.indexOf(contentType) >= 0 ||
			viewerConstants.BROWSER_VIEWER_SUPPORTED_MIME_TYPES.indexOf(contentType) >= 0)
		return true;

	return false;
};

const useBrowserViewer = (contentType) => {
	if (viewerConstants.BROWSER_VIEWER_SUPPORTED_MIME_TYPES.indexOf(contentType) >= 0)
		return true;
};

const executeContentAction = (contentType, fileSize, state, dispatch, updateState) => {
	if (useBrowserViewer(contentType)) {
		let isImage = 'true';
		if (contentType == viewerConstants.TEXT_CONTENT_TYPE) {
			isImage = 'false';
			dispatch(viewerConstants.GET_TEXT_DATA, {sysId: state.properties.sysId});
		}

		updateState({useBrowserViewer: 'true', sizeLoaded: fileSize, isImage: isImage});
	} else if (contentType != viewerConstants.PDF_CONTENT_TYPE) {
		const fileName = state.fileName.lastIndexOf('.') >= 0 ? state.fileName.slice(0,state.fileName.lastIndexOf('.')) : state.fileName;
		dispatch(viewerConstants.CONVERT_DOCUMENT_TO_PDF, {data: {sysAttachmentId: state.properties.sysId, name: fileName}});
	} else
		dispatch(viewerConstants.FETCH_AND_LOAD_PDF);
};

/**
 * Validate metadata
 */
const validateMetadata = ({action, dispatch, state, updateState}) => {
	const {payload: {result}} = action;

	if (!fileTypeSupported(result.content_type) || (state.properties.renderOfficeFormat == 'false' &&
		viewerConstants.PDFJS_VIEWER_SUPPORTED_MIME_TYPES.indexOf(result.content_type) > 0))
		// No preview is shown if file is not supported or renderOfficeFormat is false for office content types
		dispatch(viewerConstants.SHOW_NO_PREVIEW, {failure_message: viewerConstants.MESSAGE_FILE_TYPE_NOT_SUPPORTED, disable_download: 'false'});
	else if (result.size_bytes > (state.properties.maximumFileSize * 1024 * 1024))
		dispatch(viewerConstants.SHOW_NO_PREVIEW, {failure_message: viewerConstants.MESSAGE_SIZE_EXCEEDED, disable_download: 'false'});
	else if (result.state == viewerConstants.NOT_AVAILABLE)
		dispatch(viewerConstants.SHOW_NO_PREVIEW, {failure_message: viewerConstants.MESSAGE_INFECTED_FILE, disable_download: 'true'});
	else if ((result.state == viewerConstants.PENDING) || (result.state == viewerConstants.AVAILABLE_CONDITIONALLY))
		dispatch(viewerConstants.SHOW_NO_PREVIEW, {failure_message: viewerConstants.MESSAGE_SCAN_NEEDED, disable_download: 'false'});
	else
		executeContentAction(result.content_type, result.size_bytes, state, dispatch, updateState);
};

const getParentTable = (tableName) => {
	if (tableName.indexOf(viewerConstants.USER_IMAGE_PREFIX) == 0)
		tableName = tableName.slice(viewerConstants.USER_IMAGE_PREFIX.length);
	else if (tableName.indexOf(viewerConstants.INVISIBLE_PREFIX) == 0)
		tableName = tableName.slice(viewerConstants.USER_IMAGE_PREFIX.length);
	else if (tableName.indexOf(viewerConstants.ENCRYPTED_FILE_PREFIX) == 0)
		tableName = tableName.slice(viewerConstants.ENCRYPTED_FILE_PREFIX.length);

	return tableName;
};

/**
 * Set toolbar state based on viewer type
 */
const setToolbarState = ({action, state, dispatch, updateState, updateProperties}) => {
	const result = action.payload.result;
	if (state.fileName)
		updateState({fileSize: result.size_bytes});
	else
		updateState({fileName: result.file_name, fileSize: result.size_bytes});

	if (useBrowserViewer(result.content_type))
		updateProperties({...getDefaultBrowserViewerProperties()});

	// Handles delete button show/hide state
	if (state.properties.deleteHidden == 'false')
		dispatch(viewerConstants.GET_ATTACHMENTS_ACCESS_INFO, {table: getParentTable(result.table_name), sysId: result.table_sys_id});
	// Validates metadata and handles no preview flows
	dispatch(viewerConstants.VALIDATE_METADATA, {result: result});
};

/**
 * Shows no preview message and Download button
 */
const showNoPreview = ({state, action, updateState}) => {
	let {payload} = action;
	state.noPreview = 'true';
	if (payload.hasOwnProperty(viewerConstants.FAILURE_MESSAGE))
		state.noPreviewMessage = payload.failure_message;

	if (payload.hasOwnProperty(viewerConstants.DISABLE_DOWNLOAD))
		state.disableDownload = payload.disable_download;

	updateState({
		...state
	});
};

/**
 * Fetches and Loads PDF document
 */
const fetchAndLoadPDF = ({action, dispatch, state, updateState}) => {
	let pdfPassword = '';
	if (action.payload)
		// We come here only in case of password protected files
		pdfPassword = action.payload.password;

	pdfjsLib.GlobalWorkerOptions.workerSrc = worker;
	const attachmentSysId = state.convertedSysId ? state.convertedSysId : state.properties.sysId;
	const attachmentURL = viewerConstants.ATTACHMENT_CONTENT_API_PREFIX + attachmentSysId + viewerConstants.ATTACHMENT_CONTENT_API_SUFFIX;
	const loadingTask = pdfjsLib.getDocument({url: attachmentURL, password: pdfPassword, 
	httpHeaders: {
	    'X-UserToken': window.g_ck
	  },
	});
	loadingTask.onProgress = ({loaded}) => {
		updateState({sizeLoaded: loaded});
	};

	loadingTask.promise.then(function(pdfDoc) {
		var container = state.containerRef.current;
		var pdfLinkService = new pdfjsViewer.PDFLinkService();
		// Enable find controller.
		var pdfFindController = new pdfjsViewer.PDFFindController({
			linkService: pdfLinkService
		});

		var pdfViewer = new pdfjsViewer.PDFViewer({
			container: container,
			linkService: pdfLinkService,
			findController: pdfFindController
		});


		pdfLinkService.setViewer(pdfViewer);
		updateState({currentPage: 1, totalPages: pdfDoc.numPages, pdfViewer: pdfViewer, passwordError: ''});
		pdfViewer.setDocument(pdfDoc);
		pdfLinkService.setDocument(pdfDoc);
		container.addEventListener(viewerConstants.PAGE_CHANGE_EVENT, function(){dispatch(viewerConstants.PAGE_CHANGED);});
	}).catch(function(error) {
		// incorrect password
		if (error.name == viewerConstants.PASSWORD_EXCEPTION)
			updateState({passwordError: error});

		if (error.name == viewerConstants.MISSING_PDF_EXCEPTION) {
			updateState({passwordError: '', sizeLoaded: 0, fileName: ''});
			dispatch(viewerConstants.FETCH_FAILED);
		}
	});
};

/**
 * Handles pageChange
 */
const pageChanged = ({state, updateState}) => {
	state.currentPage = state.pdfViewer.currentPageNumber;

	updateState({
		...state
	});
};

/**
 * Handle failure scenarios for PDF fetch
 */
const handleFetchFailed = ({dispatch}) => {
	dispatch(viewerConstants.SHOW_NO_PREVIEW, {failure_message: viewerConstants.MESSAGE_FILE_DOES_NOT_EXIST, disable_download: 'true'});
};

/**
 * Renders previous page in PDF
 */
const renderPreviousPage = ({state, updateState}) => {
	let {pdfViewer} = state;
	let pageNumber = pdfViewer.currentPageNumber;
	if (pageNumber > 1)
		pageNumber--;

	pdfViewer.currentPageNumber = pageNumber;

	updateState({
		...state
	});
};

/**
 * Renders next page in PDF
 */
const renderNextPage = ({state, updateState}) => {
	let {pdfViewer} = state;
	let pageNumber = pdfViewer.currentPageNumber;
	if (pageNumber < state.totalPages)
		pageNumber++;

	pdfViewer.currentPageNumber = pageNumber;

	updateState({
		...state
	});
};

/**
 * Renders a page
 */
const renderPage = ({state, action, dispatch}) => {
	let {pdfViewer} = state;
	let {payload} = action;
	let target = payload.target;
	let floorValue = Math.floor(target.value);
	// Server side validation for better security
	if (floorValue >= 1 && floorValue <= state.totalPages)
		pdfViewer.currentPageNumber = floorValue;

	target.value = pdfViewer.currentPageNumber;
	dispatch(viewerConstants.PAGE_CHANGED);
};

/**
 * Zoom out PDF
 */
const zoomOut = ({state, updateState}) => {
	let {pdfViewer} = state;
	let scale = pdfViewer.currentScale;
	if (scale > viewerConstants.MIN_SCALE) {
		if (scale <= 1)
			pdfViewer.currentScale -= viewerConstants.SMALL_SCALE_CHANGE;
		else
			pdfViewer.currentScale -= viewerConstants.LARGE_SCALE_CHANGE;
	}

	updateState({
		...state
	});
};

/**
 * Zoom in PDF
 */
const zoomIn = ({state, updateState}) => {
	let {pdfViewer} = state;
	let scale = pdfViewer.currentScale;
	if (scale < viewerConstants.MAX_SCALE) {
		if (scale < 1)
			pdfViewer.currentScale += viewerConstants.SMALL_SCALE_CHANGE;
		else
			pdfViewer.currentScale += viewerConstants.LARGE_SCALE_CHANGE;
	}

	updateState({
		...state
	});
};

/**
 * Rotate PDF in clockwise direction
 */
const rotateClockwise = ({state, updateState}) => {
	let currentPage = state.pdfViewer.currentPageNumber;
	state.pdfViewer.pagesRotation += 90;
	state.pdfViewer.currentPageNumber = currentPage;

	updateState({
		...state
	});
};

/**
 * Rotate PDF in anticlockwise direction
 */
const rotateAnticlockwise = ({state, updateState}) => {
	let currentPage = state.pdfViewer.currentPageNumber;
	state.pdfViewer.pagesRotation -= 90;
	state.pdfViewer.currentPageNumber = currentPage;

	updateState({
		...state
	});
};

const setCurrentSearchResult =  (findController, updateState) => {
	const pageMatches = findController._pageMatches;
	const {pageIdx, matchIdx} = findController._selected;
	let matchNum = 0;
	for (var pageId = 0; pageId < pageIdx; pageId++)
		matchNum += pageMatches[pageId].length;

	updateState({currentMatch: matchNum + matchIdx + 1});
};

/**
 * Search PDF content for a string
 */
const searchContent = ({action, state, updateState}) => {
	let {pdfViewer} = state;
	let searchString = action.payload.searchString;
	if (searchString == '')
		return;

	pdfViewer.findController.executeCommand(viewerConstants.SEARCH_COMMAND, {
		caseSensitive: false,
		findPrevious: false,
		phraseSearch: true,
		query: searchString
	});

	// Default timeout for find is 250 ms
	setTimeout(function(){
		if (pdfViewer.findController._matchesCountTotal > 0)
			updateState({matchesCount: pdfViewer.findController._matchesCountTotal, searchState: viewerConstants.SEARCH_STATES.FOUND});
		else
			updateState({matchesCount: pdfViewer.findController._matchesCountTotal, searchState: viewerConstants.SEARCH_STATES.NOT_FOUND});

		setCurrentSearchResult(pdfViewer.findController, updateState);
	}, viewerConstants.SEARCH_WAIT_TIME);
};

/**
 * Navigate to prev search result
 */
const prevSearchResult = ({state, updateState}) => {
	let {pdfViewer: {findController}} = state;
	findController._state.findPrevious = true;
	findController._nextMatch();
	if (state.currentMatch > 1)
		updateState({currentMatch: state.currentMatch - 1});
	else
		updateState({currentMatch: state.matchesCount});
};

/**
 * Navigate to next search result
 */
const nextSearchResult = ({state, updateState}) => {
	let {pdfViewer: {findController}} = state;
	findController._state.findPrevious = false;
	findController._nextMatch();
	if (state.currentMatch < state.matchesCount)
		updateState({currentMatch: state.currentMatch + 1});
	else
		updateState({currentMatch: 1});
};

/**
 * Navigate to next search result
 */
const clearSearch = ({state, updateState}) => {
	let {pdfViewer: {findController}} = state;
	if (findController._state) {
		findController._onFindBarClose();
		findController._state.query = '';
	}

	updateState({searchString: '', searchState: viewerConstants.SEARCH_STATES.NOT_REQUESTED});
};

const submitPassword = ({dispatch, state}) => {
	dispatch(viewerConstants.SUBMIT_PASSWORD, {password: state.password});
};

/**
 * This will be triggered on click of modal close
 */
const closeModal = ({action, dispatch}) => {
	if (action.meta && action.meta.componentName == viewerConstants.DELETE_LABEL)
		dispatch(viewerConstants.CLOSE_DELETE_CONFIRMATION, {});
	else
		dispatch(viewerConstants.CLOSE_DOCUMENT_VIEWER, {});
};

/**
 * Show delete confirmation modal
 */
const showDeleteConfirmationModal = ({updateState}) => {
	updateState({deleteContainerHidden: 'false'});
};

/**
 * Close delete confirmation modal
 */
const closeDeleteConfirmationModal = ({updateState}) => {
	updateState({deleteContainerHidden: 'true'});
};

/**
 * Used to trigger the delete functionality
 */
const deleteAttachmentUtil = ({state, dispatch}) => {
	dispatch(viewerConstants.DELETE_ATTACHMENT, {sysId : state.properties['sysId']});
};

/**
 * Deletes the attachment
 */
const deleteAttachment = createHttpEffect(viewerConstants.ATTACHMENT_METADATA_URL, {
	method: viewerConstants.DELETE_LABEL.toUpperCase(),
	pathParams: [viewerConstants.SYS_ID],
	successActionType: viewerConstants.CLOSE_DOCUMENT_VIEWER,
	errorActionType: viewerConstants.DELETE_ATTACHMENT_ERROR,
	batch: false
});

/**
 * Delete Attachment Failure
 */
const deleteAttachmentError = ({updateState}) => {
	updateState({deleteContainerHidden: 'true', deleteErrorHidden: 'false'});
};

/**
 * Close the document viewer
 */
const closeDocumentViewer = ({action, dispatch, updateProperties}) => {
	if (action.meta && action.meta.hasOwnProperty('request'))
		dispatch(viewerConstants.DOCVIEWER_CLOSE_ACTION_CLICKED, {viewerHidden: true, deleted: true});
	else
		dispatch(viewerConstants.DOCVIEWER_CLOSE_ACTION_CLICKED, {viewerHidden: true});
	updateProperties({viewerHidden: 'true'});
};

const closeDeleteError = ({updateState}) => {
	updateState({deleteErrorHidden: 'true'});
};

export default {
	actionHandlers: {
		// This is dispatched onBootstrap
		FETCH_ATTACHMENT_METADATA: fetchAttachmentMetadata,
		// This is dispatched from the effect on success of fetchAttachmentMetadata
		SET_TOOLBAR_STATE: setToolbarState,
		// This is dispatched from setToolbarState
		VALIDATE_METADATA: validateMetadata,
		// This is dispatched from validateMetadata post validation
		FETCH_AND_LOAD_PDF: fetchAndLoadPDF,
		// This is dispatched if the fetch failed
		FETCH_FAILED: handleFetchFailed,
		// This is dispatched when user clicks on Previous button
		PREVIOUS_PAGE: renderPreviousPage,
		// This is dispatched when user clicks on Next button
		NEXT_PAGE: renderNextPage,
		// This is dispatched when page is changed
		PAGE_CHANGED: pageChanged,
		// This is dispatched when user types a value in page number input
		PAGE_NUMBER_CHANGED: renderPage,
		// This is dispatched when user clicks on zoom out button
		ZOOM_OUT: zoomOut,
		// This is dispatched when user clicks on zoom in button
		ZOOM_IN: zoomIn,
		// This is dispatched when user clicks on rotate clockwise button
		ROTATE_CLOCKWISE: rotateClockwise,
		// This is dispatched when user clicks on rotate anticlockwise button
		ROTATE_ANTICLOCKWISE: rotateAnticlockwise,
		// This is dispatched when user enters a password
		SUBMIT_PASSWORD: fetchAndLoadPDF,
		// This is dispatched when user enters a string for search
		SEARCH_CONTENT: searchContent,
		// This is dispatched when user clicks for prev search result
		PREV_SEARCH_RESULT: prevSearchResult,
		// This is dispatched when user clicks for next search result
		NEXT_SEARCH_RESULT: nextSearchResult,
		// This is dispatched when user wants to clear search
		CLEAR_SEARCH: clearSearch,
		// This is dispatched when preview can not be shown
		SHOW_NO_PREVIEW: showNoPreview,
		// This is dispatched on clicking delete icon
		SHOW_DELETE_CONFIRMATION: showDeleteConfirmationModal,
		// This is dispatched on closing the delete confirmation
		CLOSE_DELETE_CONFIRMATION: closeDeleteConfirmationModal,
		// This is dispatched when user confirms to delete the attachment
		DELETE_ATTACHMENT: deleteAttachment,
		// This is dispatched to make delete api call
		DELETE_ATTACHMENT_UTIL: deleteAttachmentUtil,
		// This is dispatched on delete attachment failure
		DELETE_ATTACHMENT_ERROR: deleteAttachmentError,
		// This is dispatched on document viewer close
		CLOSE_DOCUMENT_VIEWER: closeDocumentViewer,
		// This is dispatched from setToolbarState
		GET_ATTACHMENTS_ACCESS_INFO: getAttachmentsAccessInfo,
		// This is dispatched from the effect on success of getAttachmentsAccessInfo
		UPDATE_ATTACHMENT_DELETE_INFO: updateAttachmentDeleteInfo,
		// This is dispatched from executeContentAction when it is text rendering
		GET_TEXT_DATA: getTextData,
		// This is dispatched from the effect on success of getTextData
		SET_TEXT_DATA: setTextData,
		// This is dispatched from executeContentAction when the document type is docx, pptx, xlsx
		CONVERT_DOCUMENT_TO_PDF: convertDocumentToPDF,
		// This is dispatched from the effect on success of convertDocumentToPDF
		UPDATE_VIEWER_DOCUMENT: updateViewerDocument,
		// This is dispatched from the effect on error of convertDocumentToPDF
		PDF_CONVERSION_FAILED: handlePDFConversionFailure,
		// This is dispatched when user clicks on submit in password modal
		'NOW_MODAL#FOOTER_ACTION_CLICKED': submitPassword,
		// This is dispatched when user clicks on close in password modal
		'NOW_MODAL#OPENED_SET': closeModal,
		// This is dispatched when user clicks the delete error message close icon
		'NOW_ALERT#ACTION_CLICKED': closeDeleteError
	}
};
