// GraphQL Query to get attachments access info for given record
export const ATTACHMENT_GRAPHQL_QUERY = `
query ($table: String!, $sysId: String!) {
	query: GlideAttachmentQuery_Query {
		attachments: attachments(table: $table, sysId: $sysId) {
			_results: records{
				canDelete,
				sysId
			}
		}
	}
}
`;
