import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-alert';
import classNames from 'classnames';
import * as viewerConstants from './constants.js';
import {t} from 'sn-translate';
import '@servicenow/now-tooltip';
import '@servicenow/now-modal';
import '@devsnc/sn-record-input';


/**
 * View --> File name as heading, buttons to perform actions and canvasContainer to render PDF
 */
export default (state, {dispatch, updateState}) => {
	const {fileName, containerRef, fileSize, sizeLoaded, pdfViewer, totalPages, currentPage, passwordError, searchString,
		searchState, searchInputRef, noPreview, noPreviewMessage, disableDownload, password, useBrowserViewer, deleteContainerHidden,
		deleteErrorHidden, matchesCount, currentMatch} = state;
	const {fileNameRef, searchButtonRef, prevSearchButtonRef, nextSearchButtonRef, clearSearchButtonRef, prevPageButtonRef, nextPageButtonRef,
		zoomOutButtonRef, zoomInButtonRef, rotateClockwiseButtonRef, rotateAnticlockwiseButtonRef, downloadButtonRef, closeButtonRef, deleteButtonRef, isImage, textData} = state;
	const {properties: {viewerHidden, searchDisabled, navigationDisabled, zoomDisabled, rotationDisabled, downloadDisabled,
		closeHidden, deleteHidden, toolbarHidden, darkMode}} = state;
	let currentScale = pdfViewer.currentScale;
	// Set button state
	let fileLoaded = (sizeLoaded == fileSize) && (passwordError == '');
	let previousDisabled = (currentPage <= 1);
	let nextDisabled = (currentPage == totalPages);
	let zoomOutDisabled = (currentScale <= viewerConstants.MIN_SCALE) || !fileLoaded;
	let zoomInDisabled = (currentScale >= viewerConstants.MAX_SCALE) || !fileLoaded;
	let passwordContainerHidden = (passwordError == '');
	let invalidPassword = 'false';
	let messages = [];
	if (!passwordContainerHidden && passwordError.code == 2) {
		invalidPassword = 'true';
		messages = [{type: 'error', message: t('{0}', viewerConstants.INVALID_PASSWORD_MESSAGE)}];
	}

	const downloadURL = viewerConstants.ATTACHMENT_URL_PREFIX + state.properties.sysId;
	const downloadLabel = t('{0}', viewerConstants.DOWNLOAD) + fileName;
	const searchInProgress = (searchState == viewerConstants.SEARCH_STATES.FOUND || searchState == viewerConstants.SEARCH_STATES.NOT_FOUND);

	const deleteTooltipLabel = t('{0} ', viewerConstants.DELETE) + fileName;
	const deleteHeaderLabel = t('{0}', viewerConstants.CONFIRMATION_MSG);
	const deleteContent = t('{0}', viewerConstants.DELETE_MSG);

	let fileNameClassNames = classNames('fileNameSearch');
	if (searchDisabled == 'true' || !fileLoaded)
		fileNameClassNames = classNames('fileName');

	let errorIcon = 'triangle-exclamation-outline';
	if (noPreviewMessage == viewerConstants.MESSAGE_INFECTED_FILE)
		errorIcon = 'document-infected-outline';
	else if (noPreviewMessage == viewerConstants.MESSAGE_SCAN_NEEDED)
		errorIcon = 'change-outline';
	else if (noPreviewMessage == viewerConstants.MESSAGE_SIZE_EXCEEDED)
		errorIcon = 'circle-exclamation-outline';

	let searchResults = '';
	if (searchInProgress)
		if (matchesCount == 1)
			searchResults = matchesCount + t(' result');
		else if (matchesCount == 0)
			searchResults = matchesCount + t(' results');
		else
			searchResults = currentMatch + t(' of ') + matchesCount + t(' results');

	let viewerClassNames = {};
	if (darkMode == 'true')
		viewerClassNames.darkMode = true;
	else
		viewerClassNames.lightMode = true;

	if (toolbarHidden == 'true')
		viewerClassNames.toolbarHidden = true;

	viewerClassNames = classNames(viewerClassNames);

	return (
		<div id="viewer-container" className={viewerClassNames} hidden={viewerHidden == 'true'}>
			<now-modal id="password-modal" header-label={t('{0}', viewerConstants.PASSWORD_PROTECTED_DOCUMENT_MESSAGE)}
				footer-actions='[{"label": "Submit", "variant": "primary"}]' opened={!passwordContainerHidden}>
				<sn-record-input id="pdf-password" type="password" maxlength="80" label={t('Password')} value={password} aria-label={t('Enter Password')}
					onValueChange={(e) => {
						updateState({password: e.value});
					}}
					onkeypress={(event) => {
						if (event.which == 13)
							dispatch(viewerConstants.SUBMIT_PASSWORD, {password: event.target.value});
					}} invalid={invalidPassword} messages={messages}/>
			</now-modal>
			<now-modal id="delete-modal" component-name={viewerConstants.DELETE_LABEL} header-label={deleteHeaderLabel} content = {deleteContent}
				footer-actions='[{"label": "Delete", "variant": "primary-negative", "clickActionType": "DELETE_ATTACHMENT_UTIL"},{"label": "No","clickActionType": "CLOSE_DELETE_CONFIRMATION"}]' opened={deleteContainerHidden == 'false'} size="sm">
			</now-modal>

			<div id="toolbar-viewer" hidden={toolbarHidden == 'true'}>
				<div id="toolbar-viewer-left">
					<span tab-index="0" aria-label={fileName} id="file-name" hook-insert={({elm}) => updateState({fileNameRef: elm})}
						hook-destroy={() => updateState({fileNameRef: null})} aria-describedby="tooltip-file-name" className={fileNameClassNames}
						hidden={fileName == ''}>{fileName}</span>
					<now-tooltip id="tooltip-file-name" target-ref={fileNameRef} content={fileName}/>
					<div id="separator" className={fileNameClassNames} hidden={fileName == ''}></div>
					<div id="search-container" hidden={searchDisabled == 'true' || !fileLoaded}>
						<button id="search-button" hook-insert={({elm}) => updateState({searchButtonRef: elm})}
							hook-destroy={() => updateState({searchButtonRef: null})} aria-describedby="tooltip-search-button" aria-label={t('Search document')}
							onclick={() => dispatch(viewerConstants.SEARCH_CONTENT, {searchString: searchInputRef.current.value})} disabled={!fileLoaded}>
							<now-icon icon="magnifying-glass-outline"></now-icon>
						</button>
						<now-tooltip id="tooltip-search-button" target-ref={searchButtonRef} content={t('Search document')}/>
						<div id="search-content">
							<input id="search-input" ref={searchInputRef} aria-label={t('Search document')} value={searchString} placeholder={t('Search document')}
								onkeyup={(event) => {
									if (event.which == 13)
										dispatch(viewerConstants.SEARCH_CONTENT, {searchString: event.target.value});
									else {
										if (event.target.value == '')
											dispatch(viewerConstants.CLEAR_SEARCH);
										updateState({searchString: event.target.value});
									}
								}} disabled={!fileLoaded}>
							</input>
							<div id="search-results" hidden={!searchInProgress}>{searchResults}</div>
							<div id="search-separator" hidden={!fileLoaded || !searchInProgress}></div>
							<button id="prev-result-button" hook-insert={({elm}) => updateState({prevSearchButtonRef: elm})}
								hook-destroy={() => updateState({prevSearchButtonRef: null})} aria-describedby="tooltip-prev-search-button" aria-label={t('Previous search result')}
								onclick={() => dispatch(viewerConstants.PREV_SEARCH_RESULT)} hidden={!fileLoaded || !searchInProgress} disabled={matchesCount <= 1}>
								<now-icon icon="chevron-up-outline" size="sm"></now-icon>
							</button>
							<now-tooltip id="tooltip-prev-search-button" target-ref={prevSearchButtonRef} content={t('Previous search result')}/>
							<button id="next-result-button" hook-insert={({elm}) => updateState({nextSearchButtonRef: elm})}
								hook-destroy={() => updateState({nextSearchButtonRef: null})} aria-describedby="tooltip-next-search-button"
								aria-label={t('Next search result')} onclick={() => dispatch(viewerConstants.NEXT_SEARCH_RESULT)} hidden={!fileLoaded || !searchInProgress}
								disabled={matchesCount <= 1}>
								<now-icon icon="chevron-down-outline" size="sm"></now-icon>
							</button>
							<now-tooltip id="tooltip-next-search-button" target-ref={nextSearchButtonRef} content={t('Next search result')}/>
							<button id="search-close" hook-insert={({elm}) => updateState({clearSearchButtonRef: elm})}
								hook-destroy={() => updateState({clearSearchButtonRef: null})} aria-describedby="tooltip-clear-search-button"
								aria-label={t('Clear search')} onclick={() => dispatch(viewerConstants.CLEAR_SEARCH)} hidden={!fileLoaded || (searchString == '')}>
								<now-icon icon="close-outline" size="sm"></now-icon>
							</button>
							<now-tooltip id="tooltip-clear-search-button" target-ref={clearSearchButtonRef} content={t('Clear search')}/>
						</div>
					</div>
				</div>
				<div id="toolbar-viewer-middle">
					<button id="prev-page" hook-insert={({elm}) => updateState({prevPageButtonRef: elm})}
						hook-destroy={() => updateState({prevPageButtonRef: null})} aria-describedby="tooltip-prev-page-button"
						aria-label={t('Previous page')} onclick={() => dispatch(viewerConstants.PREVIOUS_PAGE)} disabled={previousDisabled} hidden={navigationDisabled == 'true' || !fileLoaded}>
						<now-icon icon="arrow-up-outline"></now-icon>
					</button>
					<now-tooltip id="tooltip-prev-page-button" target-ref={prevPageButtonRef} content={t('Previous page')}/>
					<input type="number" id="page-number" aria-label={t('Page number')} value={currentPage} size="4" step="1" min="1" max={totalPages}
						onchange={(event) => {dispatch(viewerConstants.PAGE_NUMBER_CHANGED,
							{target: event.target});}} disabled={!fileLoaded} hidden={navigationDisabled == 'true' || !fileLoaded}>
					</input>
					<span className="numPages" hidden={navigationDisabled == 'true' || !fileLoaded}>{t('of')}</span>
					<span id="page-count" className="numPages" hidden={navigationDisabled == 'true' || !fileLoaded}>{totalPages}</span>
					<button id="next-page" hook-insert={({elm}) => updateState({nextPageButtonRef: elm})}
						hook-destroy={() => updateState({nextPageButtonRef: null})} aria-describedby="tooltip-next-page-button"
						aria-label={t('Next page')} onclick={() => dispatch(viewerConstants.NEXT_PAGE)} disabled={nextDisabled} hidden={navigationDisabled == 'true' || !fileLoaded}>
						<now-icon icon="arrow-down-outline"></now-icon>
					</button>
					<now-tooltip id="tooltip-next-page-button" target-ref={nextPageButtonRef} content={t('Next page')}/>
				</div>
				<div id="toolbar-viewer-right">
					<a id="toolbar-download" tab-index="-1" href={downloadURL} aria-label={downloadLabel} hidden={downloadDisabled == 'true' || (!fileLoaded)} download>
						<button id="toolbar-download-button" hook-insert={({elm}) => updateState({downloadButtonRef: elm})}
							hook-destroy={() => updateState({downloadButtonRef: null})} aria-describedby="tooltip-download-button"
							aria-label={downloadLabel} disabled={!fileLoaded}>
							<now-icon icon="download-outline"></now-icon>
						</button>
						<now-tooltip id="tooltip-download-button" target-ref={downloadButtonRef} content={downloadLabel}/>
					</a>
					<button id="zoom-in" hook-insert={({elm}) => updateState({zoomInButtonRef: elm})}
						hook-destroy={() => updateState({zoomInButtonRef: null})} aria-describedby="tooltip-zoom-in-button"
						aria-label={t('Zoom in')} onclick={() => dispatch(viewerConstants.ZOOM_IN)} disabled={zoomInDisabled} hidden={zoomDisabled == 'true' || !fileLoaded}>
						<now-icon icon="magnifying-glass-plus-outline"></now-icon>
					</button>
					<now-tooltip id="tooltip-zoom-in-button" target-ref={zoomInButtonRef} content={t('Zoom in')}/>
					<button id="zoom-out" hook-insert={({elm}) => updateState({zoomOutButtonRef: elm})}
						hook-destroy={() => updateState({zoomOutButtonRef: null})} aria-describedby="tooltip-zoom-out-button"
						aria-label={t('Zoom out')} onclick={() => dispatch(viewerConstants.ZOOM_OUT)} disabled={zoomOutDisabled} hidden={zoomDisabled == 'true' || !fileLoaded}>
						<now-icon icon="magnifying-glass-minus-outline"></now-icon>
					</button>
					<now-tooltip id="tooltip-zoom-out-button" target-ref={zoomOutButtonRef} content={t('Zoom out')}/>
					<button id="rotate-clockwise" hook-insert={({elm}) => updateState({rotateClockwiseButtonRef: elm})}
						hook-destroy={() => updateState({rotateClockwiseButtonRef: null})} aria-describedby="tooltip-rotate-clockwise-button"
						aria-label={t('Rotate clockwise')} onclick={() => dispatch(viewerConstants.ROTATE_CLOCKWISE)} disabled={!fileLoaded} hidden={rotationDisabled == 'true' || !fileLoaded}>
						<now-icon icon="arrow-clockwise-outline"></now-icon>
					</button>
					<now-tooltip id="tooltip-rotate-clockwise-button" target-ref={rotateClockwiseButtonRef} content={t('Rotate clockwise')}/>
					<button id="rotate-anticlockwise" hook-insert={({elm}) => updateState({rotateAnticlockwiseButtonRef: elm})}
						hook-destroy={() => updateState({rotateAnticlockwiseButtonRef: null})} aria-describedby="tooltip-rotate-anticlockwise-button"
						aria-label={t('Rotate anticlockwise')} onclick={() => dispatch(viewerConstants.ROTATE_ANTICLOCKWISE)} disabled={!fileLoaded} hidden={rotationDisabled == 'true' || !fileLoaded}>
						<now-icon icon="arrow-counterclockwise-outline"></now-icon>
					</button>
					<now-tooltip id="tooltip-rotate-anticlockwise-button" target-ref={rotateAnticlockwiseButtonRef} content={t('Rotate anticlockwise')}/>
					<button id="delete-attachment" hook-insert={({elm}) => updateState({deleteButtonRef: elm})}
						hook-destroy={() => updateState({deleteButtonRef: null})} aria-describedby="tooltip-delete-attachment"
						aria-label={t('Delete attachment')} onclick={() => dispatch(viewerConstants.SHOW_DELETE_CONFIRMATION)} hidden={deleteHidden == 'true' ||
						(!fileLoaded && ((noPreview == 'false') || (noPreviewMessage == viewerConstants.MESSAGE_FILE_DOES_NOT_EXIST)))}>
						<now-icon icon="trash-outline"></now-icon>
					</button>
					<now-tooltip id="tooltip-delete-attachment" target-ref={deleteButtonRef} content={deleteTooltipLabel}/>
					<button id="close-viewer" hook-insert={({elm}) => updateState({closeButtonRef: elm})}
						hook-destroy={() => updateState({closeButtonRef: null})} aria-describedby="tooltip-close-button"
						aria-label={t('Close viewer')} onclick={() => dispatch('CLOSE_DOCUMENT_VIEWER', {viewerHidden: 'true'})} hidden={closeHidden == 'true'}>
						<now-icon icon="close-outline"></now-icon>
					</button>
					<now-tooltip id="tooltip-close-button" target-ref={closeButtonRef} content={t('Close viewer')}/>
				</div>
			</div>
			<progress id="loading-bar" value={sizeLoaded} max={fileSize} hidden={sizeLoaded == fileSize || noPreview == 'true'}></progress>
			<div className="delete-error-container" hidden={deleteErrorHidden=='true'}>
				<now-alert icon="circle-exclamation-outline" status="critical" action={{type: 'dismiss'}} content={t('{0}', viewerConstants.DELETE_ERROR_MSG)}></now-alert>
			</div>
			<div id="canvas-container" ref={containerRef}>
				<div id="viewer" className="pdfViewer"></div>
				<div id="image-container" hidden={useBrowserViewer == 'false' || (isImage == 'false') ||  (noPreview == 'true')}>
					<img className="img-viewer" src={downloadURL} alt={fileName}></img>
				</div>
				<div id="text-container" hidden={useBrowserViewer == 'false' || (isImage == 'true') || (noPreview == 'true')}>
					<pre className="text-body">{textData}</pre>
				</div>
				<div id="no-preview" hidden={noPreview == 'false'}>
					<now-icon id="error-icon" icon={errorIcon} size="xl"></now-icon>
					<span id="error-text" aria-label={t('{0}', noPreviewMessage)}>{t('{0}', noPreviewMessage)}</span>
					<a tab-index="-1" href={downloadURL} aria-label={downloadLabel} hidden={downloadDisabled == 'true'}>
						<now-button id="download-button" variant="secondary" size="md" aria-label={downloadLabel} hidden={disableDownload == 'true'}>{t('Download')}</now-button>
					</a>
				</div>
			</div>
		</div>
	);
};
